.header {
  background: #fff;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-width: 700px;
  margin: 0;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  font-size: 0;
}

.nav {
  align-items: center;
  display: flex;
}

.nav__list {
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link:active {
  text-decoration: none;
}

.nav__link[data-qa] {
  position: relative;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

:root {
  --column-count: 1;
}

* {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

h3, img, a, p {
  margin: 0;
  padding: 0;
}

.cards {
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .cards {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .cards {
    --column-count: 4;
  }
}

.product {
  color: #060b35;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  font-size: 12px;
  line-height: 18px;
}

.product:hover {
  transition: all .3s;
  transform: scale(1.2);
}

.product__title {
  margin: 0;
  font-size: 12px;
  font-weight: 500;
}

.product:hover .product__title {
  color: #34568b;
}

.product__photo {
  align-items: center;
  width: 160px;
  height: 134px;
  margin: 0 auto 35px;
  padding-left: 3px;
}

.product__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product__review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.product__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product__price-text {
  color: #616070;
}

.product__price-value {
  font-size: 16px;
  font-weight: 700;
}

.product__buy {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  padding-block: 10px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: inline-block;
}

.product__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  align-items: center;
  height: 16px;
  display: flex;
}

.stars__star {
  cursor: pointer;
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin: 0;
}

.stars__star:not(:first-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

